import React, {Component} from 'react'
import MovieData from './movies.json'
import { HorizontalScroll, Avatar } from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'
import './style.css'
import ScreenIMG from './screenImg'

class DataList extends Component {
    render() {
        return (
            <div class="movieBlock">
                {MovieData.map((postDetail)=>{
                return (
                    <div class="movieItem">
                        <div class="movieItem__imgBlock">
                            <img src={postDetail.imageURL}/>
                            <div class="movieItem__title">{postDetail.title}</div>
                        </div>
                        <div class="movieItem__desc">{postDetail.desc}</div>
                    </div>
                )
                })}
            </div>
        )
    }
}

export default DataList;