import React, {Component} from 'react'
import images from './movies.json'
import { HorizontalScroll, Avatar } from '@vkontakte/vkui'
import '@vkontakte/vkui/dist/vkui.css'
import './style.css'

class ScreenData extends Component {
    render() {
        const itemStyle = {
            flexShrink: 0,
            width: 80,
            height: 94,
            display: 'flex',
            flexDirection:
            'column',
            alignItems: 'center',
            fontSize: 12
        };
        return (
            <HorizontalScroll>
                <div style={{ display: 'flex' }}>                        
                    {images.map((photo, index)=>{
                        return (
                            <div style={{ ...itemStyle, paddingRight: 4 }}>{photo['screenshots'].url}</div>
                        )
                    })}

                    {/* {images.map(image, index => <div style={{ ...itemStyle, paddingRight: 4 }}>{image.screenshots}</div>)} */}

                </div>
            </HorizontalScroll>
        )
    }
}

export default ScreenData;